import React, { useState, useEffect } from 'react';
import { Worker, Viewer } from '@react-pdf-viewer/core';
import '@react-pdf-viewer/core/lib/styles/index.css';
import '@react-pdf-viewer/default-layout/lib/styles/index.css';
import Cover from './Asstes/Cover.pdf';
import './App.css';

function App() {
  const [defaultScale, setDefaultScale] = useState(1.3); // Initial scale

  // Update defaultScale based on viewport width
  useEffect(() => {
    const handleResize = () => {
      const viewportWidth = window.innerWidth;
      setDefaultScale(viewportWidth <= 420 ? 0.6 : 1.3); // Adjust scale for mobile/desktop
    };

    window.addEventListener('resize', handleResize);
    handleResize(); // Call initially too

    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <div className="pdf-container">
      <Worker workerUrl={`https://unpkg.com/pdfjs-dist@3.11.174/build/pdf.worker.min.js`}>
        <Viewer fileUrl={Cover} defaultScale={defaultScale} />
      </Worker>
    </div>
  );
}

export default App;
